//对于axios进行二次封装
import axios from 'axios'

//利用axios对象的方法creat 创建axios实例
//request就是axios 只不过稍微配置一下
const requests = axios.create({
    //配置对象
    baseURL:'/mock',  //基础路径，
    timeout:5000,       //请求超时时间
})

//请求拦截器：在发送请求前，请求拦截器可以检测到，可以在请求发出前调用
requests.interceptors.request.use((config)=>{
    //config：配置对象 header请求头等重要属性
    // console.log('request')
    return config;
})
//响应拦截器：在发送请求前，请求拦截器可以检测到，可以在请求发出前调用
requests.interceptors.response.use(
    //成功回调函数  服务器响应数据返回时可以检测到 并调用
    (res)=>{
        // console.log(res.data)
        return res.data
    },
    //响应失败的回调函数
    (err)=>{
        console.log('err')
        return Promise.reject(err)
    }
)
export default requests