import { reqGetCarouselList } from "@/api"

const state = {
    carouselList:[],
    timer:{}
}
const mutations = {
    GETCAROUSELLIST(state,carouselList){
        for(let i = 0; i<carouselList.length; i++){
            if(i == 0){
                carouselList[i].show = 'show'
            }else{
                carouselList[i].show = 'hidden'
            }
        }
        state.carouselList = carouselList
    },
    HIDEAPP(state,id){
        state.carouselList[id].show = 'hidden'
    },
    SHOWAPP(state,id){
        state.carouselList[id].show = 'show'
    }
}
const actions = {
    async getCarouselList({commit}){
        let result = await reqGetCarouselList()
        if(result.code == 200){
            commit("GETCAROUSELLIST",result.data)
        }
    },
    pre_app(context){
        let cur = context.getters.cur
        context.commit('HIDEAPP',cur)
        // this.carouselList[cur].show = 'hidden'
        if(cur == 0){
            // console.log(context.state.carouselList)
            context.commit('SHOWAPP',context.state.carouselList.length-1)
            // this.carouselList[this.carouselList.length-1].show = 'show'
        }else{
            context.commit('SHOWAPP',cur-1)
            // this.carouselList[cur-1].show = 'show'
        }
    },
    next_app(context){
        let cur = context.getters.cur
        context.commit('HIDEAPP',cur)
        if(cur+1 == context.state.carouselList.length){
            // this.carouselList[0].show = 'show'
            context.commit('SHOWAPP',0)
        }else{
            // this.carouselList[cur+1].show = 'show'
            context.commit('SHOWAPP',cur+1)
        }
    },
    change_app(context,index){
        let cur = context.getters.cur
        if(cur == index){
            return
        }else{
            context.state.carouselList[cur].show = 'hidden'
            context.commit("HIDEAPP",cur)
            context.commit("SHOWAPP",index)
        }
    }
}
const getters = {
    cur(state){
        if(state.carouselList.length){
            let cur = state.carouselList.findIndex((item)=>{return item.show === 'show'})
            return cur
        }else{
            return 0
        }
    }
}

export default{
    state,
    mutations,
    actions,
    getters
}