<template>
    <div class="main_content">
        <div class="recommend" @mouseenter="stopInterval()" @mouseleave="continueInterval()">
            <div class="carousel">
                <h2>精选与推荐</h2>
                <div class="left arrow" @click="pre()">
                    <div></div>
                </div>
                <div class="right arrow" @click="next()">
                    <div></div>
                </div>
                <div class="carousel-page" v-for="carouselItem in carouselList" :key="carouselItem.AppID" :class="carouselItem.show">
                    <img :src="carouselItem[`main-img`]" alt="" v-show="mImg.show">
                    <img :src="mImg.img" alt="" v-show="!mImg.show">
                    <div class="info">
                        <div class="app_name">{{carouselItem.AppName}}</div>
                        <div class="screenshots" @mouseleave="previewout()">
                            <div class="screenshot" 
                                v-for="img in carouselItem.screenshot" 
                                :key="img.index" 
                                @mouseenter="preview($event)">
                                <div :style="`background-image: url(`+img+`)`"></div>
                            </div>
                            
                        </div>
                        <div class="details">
                            <p>现已推出</p>
                        </div>
                    </div>
                </div>
                
            </div>
            <div class="bars">
                <div class="navbar" 
                v-for="(carouselItem, index) in carouselList" 
                :key="index" :class="carouselItem.show" 
                :index="index"  @click="changeCur($event)">
                </div>
            </div> 
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:"Carousel",
    data() {
        return {
            timer:{},
            mImg:{
                show:true,
                img:''
            }

        }
    },
    computed:{
        carouselList(){
            console.log('List')
            let list = this.$store.state.carousel.carouselList
            if(list.length){
                return this.$store.state.carousel.carouselList
            }
            if(typeof this.timer !='number'){
                this.timer = setInterval(()=>{this.next()} ,2900)
                console.log('timer')
            }
        }
    },
    methods: {
        //上一张
        pre(){
            this.$store.dispatch('pre_app')
        },
        //下一张图片
        next(){
            this.$store.dispatch('next_app')
        },
        //通过底端按钮切换图片
        changeCur(e){
            let index = e.target.getAttribute('index')
            this.$store.dispatch('change_app',index)
        },
        //鼠标进入时停止自动播放
        stopInterval(){
            clearInterval(this.timer)
            console.log('mousein:'+ typeof this.timer)
            this.timer = {}
            
        },
        //鼠标离开时继续播放
        continueInterval(){
                if(typeof this.timer == 'number'){
                    console.log('mouseleave:'+this.timer+'continue')
                }else{
                    this.timer = setInterval(()=>{this.next()} ,2900)
                    console.log('new timer')
                }
                
                
        },
        //截图放大预览
        preview(event){
            let img = event.target.children[0].style.backgroundImage.slice(5,-2)
            this.mImg = {
                show:false,
                img:img
            }
            console.log(img)
        },
        //离开截图回到原图
        previewout(){
            this.mImg.show = true
        }

    },
    mounted() {
    },
    beforeDestroy() {
        clearInterval(this.timer)
    },
}
</script>

<style lang="less" scoped>
    .main_content{
        background: url('https://store.akamai.steamstatic.com/public/images/v6/colored_body_top.png?v=2') center top no-repeat;
        min-height: 800px;
        .carousel{
            cursor: pointer;
            position: relative;
            h2{
                cursor: text;
                padding-top: 20px;
                padding-bottom: 10px;
        }
        width: 985px;
        margin: 0 auto;
        .carousel-page{
            // width: 616px;
            height: 353px;
            box-shadow: 0 0 7px 0px #000;
            &.hidden{
                display: none;
            }
            img{
                width: 616px;
                height: 353px;
                opacity: 1;
                transition: opacity 1s;
            }
            .info{
                vertical-align: top;
                width: 369px;
                height: 353px;
                display: inline-block;
                background: url('https://store.cloudflare.steamstatic.com/public/images/v6/home/background_maincap_2.jpg');
                transition: all 2s;
                .app_name{
                    display: flex;
                    align-items: center;
                    padding-left: 15px;
                    height: 69px;
                    // font-weight: 300;
                    font-size: 24px;
                    padding-bottom: 4px;
                    padding-top: 4px;
                    // line-height: 69px;
                    color: #fff;
                }
            } 
        }
        
        
        .screenshots{
            width: 344px;
            padding-left: 15px;
            .screenshot{
                opacity: 0.6;
                display: inline-block;
                padding: 10px 10px 0 0 ;
                div{
                width: 162px;
                height: 69px;
                
                background-size: cover;
                background-position: center center;
                }
                &:hover{
                    opacity: 1;
                }
            }
        }
        .details{
            p{
                color:#FFFFFF;
                font-size:21px;
                padding-left: 15px;
            }
        }
        }
        .arrow{
            position: absolute;
            top: 172px;
            margin-top: -36px;
            padding: 36px 11px;
            cursor: pointer;
            &>div{
                background-image: url('https://store.st.dl.eccdnx.com/public/images/v6/arrows.png');
                background-repeat: no-repeat;
                width: 23px;
                height: 36px;
            }
            &.left{
                left: -46px;
                background:linear-gradient(to right, rgba( 0, 0, 0, 0.3) 5%,rgba( 0, 0, 0, 0) 95%);
                &>div{
                    background-position-x: -23px;
                }
                &:hover{
                    background:linear-gradient(to right, #45657A 5%,rgba( 0, 0, 0, 0) 95%);
                }
            }
            &.right{
                right: -46px;
                background:linear-gradient(to left, rgba( 0, 0, 0, 0.3) 5%,rgba( 0, 0, 0, 0) 95%);
                &:hover{
                    background:linear-gradient(to left, #45657A 5%,rgba( 0, 0, 0, 0) 95%);
                }
            }

        }
        .bars{
            text-align: center;
            .navbar{
                        display: inline-block;
                        margin: 12px 2px;    
                        width: 15px;
                        height: 9px;
                        border-radius: 2px;
                        background-color: rgb(73,83,96);
                        cursor: pointer;
                        &.show{
                            background-color: rgb(118,126,136);
                        }
                        &:hover{
                            background-color: rgb(96,105,116 );
                        }
                    }
        }
    }
    
    
</style>